import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, ProducaoRural } from '../../../../models/dtos/mfp/producaoRural';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

interface ProducaoRuralEditViewProps extends EditViewProps<ProducaoRural> {}

const labelWidth: number = 320;

const ProducaoRuralEditView: FC<ProducaoRuralEditViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const selectedItem = getSelectedItem(props);
  const empresaMFPDTO = globalReducer.globalParameter.empresaMFP;
  const formProps = useForm<ProducaoRural>({
    defaultValues: { ...selectedItem, empresaMFP: { id: empresaMFPDTO.id } },
    mode: 'onBlur',
  });

  return (
    <WindowComponent title="Manutenção de Natureza da Rubrica">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline hidden={true} formProps={formProps} id="empresaMFP.id" />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              id="referencia"
              isPrependLabel={true}
              label="Referência"
              placeholder="Informe a Referência"
              type={InputType.REFERENCE}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              id="vrPessoaJuridica"
              isPrependLabel={true}
              label="Valor Comercializado p/pessoa jurídica"
              labelWidth={labelWidth}
              placeholder="Informe o valor."
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              id="vrPessoaFisica"
              isPrependLabel={true}
              label="Valor Comercializado p/pessoa Física"
              labelWidth={labelWidth}
              placeholder="Informe o valor."
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ProducaoRuralEditView;
